@import url(https://fonts.googleapis.com/css?family=Noto+Sans|Noto+Sans+KR);
.home-wrap{height:100vh;width:100vw}.home-wrap button{position:fixed;bottom:50px;bottom:calc(env(safe-area-inset-bottom) / 2 + 50px);left:50%;transform:translateX(-50%) translateY(100px);box-shadow:3px 3px 20px 5px rgba(0,0,0,0.2);opacity:0;animation:0.3s 2s buttonMotion ease-out forwards}@keyframes buttonMotion{0%{opacity:0;transform:translateX(-50%) translateY(100px)}100%{opacity:1;transform:translateX(-50%) translateY(0)}}

.page-template{position:relative;display:flex;flex-direction:column;height:100%}

#room{display:flex;justify-content:center;align-items:center}#room .gate-container{height:100vh}.chat-list-wrap{height:calc(100vh - 208px);overflow-y:auto}.chat-list-wrap ul{margin-bottom:20px;list-style:none}.chat-list-wrap ul li{width:100%;margin-bottom:15px}.chat-list-wrap .nickname{width:80%;margin-bottom:2px;font-size:12px;text-overflow:ellipsis;overflow:hidden;white-space:nowrap}.chat-list-wrap .mine:after{clear:both !important;display:block;content:''}.chat-list-wrap .mine .nickname{display:none}.chat-list-wrap .mine .body{float:right;background:var(--chakra-colors-yellow-400);white-space:pre-line;word-break:break-all}.chat-list-wrap .body{display:inline-block;padding:5px 10px;background-color:#fff;border-radius:5px}.chat-input-wrap{height:100px}.chat-input-wrap textarea{width:260px;height:90px}

#setting{display:flex;justify-content:center;align-items:center;height:100vh;font-size:50px}@media (max-width: 600px){#setting{justify-content:flex-start}}

.color-picker{display:flex;justify-content:center;align-items:center;color:black;font-size:11px}.color-picker>input{text-align:center;width:30px;height:30px;border:1px solid #e6e6e6;background-color:white}

.tool-size-selector{display:flex;justify-content:center;align-items:center;color:black;font-size:11px}.tool-size-selector>input{text-align:center;width:30px;height:30px;border:1px solid #e6e6e6;background-color:white}.tool-size-selector>button{width:30px;height:30px;background-color:white;border:1px solid #e6e6e6;border-radius:50%}

#practice-room{display:flex;justify-content:center;align-items:center;height:100vh;font-size:50px}#practice-room canvas{background-color:white}

*{margin:0;padding:0;box-sizing:border-box;border:0;font-family:system-ui, 'AppleSDGothicNeo', sans-serif;font-style:normal;word-wrap:break-word;word-break:keep-all;-webkit-hyphens:auto;-ms-hyphens:auto;hyphens:auto}*:focus{outline:none}input:disabled{background-color:#ebebe4}button{cursor:pointer;border:0;background:none}body{line-height:1.6;font-size:16px;font-family:'Noto Sans KR', 'Noto Sans', 'Malgun Gothic', sans-serif;color:#222;background:#1a202c}

